  padding: 0;
}

body {
  font-family: "Roboto", sans-serif;
  height: 100vh;
  width: 100vw;

  margin: 0 10px 0 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
