@use '../../utils/styles/tools';

.btn-wrapper {
  width: 100%;
  margin: 5px 0 10px 0;
  display: flex;
  justify-content: center;

  .add-btn {
    width: 80%;
    padding: 5px;
    text-align: center;
    color: white;
    cursor: pointer;
    background-color: yellowgreen;
    border-radius: 10px;

    &:hover {
      background-color: #282c34;
    }
  }

  .nonactive {
    background-color: gray;
    cursor: progress;

    &:hover {
      background-color: gray;
    }
  }
}

.list-wrapper {
  height: 200px;
  margin: 5px 0 0 0;
  max-height: 200px;
  overflow-y: auto;

  .loading {
    width: 100%;
    height: 100%;
    @include tools.center-vh;
  }

  .complete {
    display: none;
  }

  .list {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
