@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,300&display=swap);
html{overflow:hidden}*{margin:0;padding:0}body{font-family:"Roboto",sans-serif;height:100vh;width:100vw;margin:0 10px 0 0;display:flex;flex-direction:column;align-items:center;justify-content:center}
.App{display:grid;grid-template-columns:1fr 4fr;width:calc(100vw - 20px);max-width:500px;max-height:400px}.App-header{width:100%;height:100%;background-color:#282c34;display:flex;flex-direction:column;align-items:center;justify-content:center}main{border:2px solid #282c34}main .container{width:100%;height:100%;background-color:#fff}main .container .title{text-align:center;padding:15px}main .container .todos{position:relative;width:100%;height:300px;display:flex;flex-direction:column}
.btn-wrapper{width:100%;margin:5px 0 10px 0;display:flex;justify-content:center}.btn-wrapper .add-btn{width:80%;padding:5px;text-align:center;color:#fff;cursor:pointer;background-color:#9acd32;border-radius:10px}.btn-wrapper .add-btn:hover{background-color:#282c34}.btn-wrapper .nonactive{background-color:gray;cursor:progress}.btn-wrapper .nonactive:hover{background-color:gray}.list-wrapper{height:200px;margin:5px 0 0 0;max-height:200px;overflow-y:auto}.list-wrapper .loading{width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.list-wrapper .complete{display:none}.list-wrapper .list{width:100%;display:flex;flex-direction:column;align-items:center}
.icon{cursor:pointer;margin:0 5px}.Bar{width:80%;margin:3px 0;padding:5px;border:1.5px solid #000;border-radius:5px}.Bar .head{display:flex;flex-wrap:nowrap;justify-content:space-between;cursor:pointer}.Bar .head .text{font-size:large}.Bar .hidden{display:none}.Bar .show{display:flex;flex-direction:column;align-items:center}.Bar .show .info{width:80%;margin:10px;border:1px #000 solid;border-radius:5px}.Bar .show .info .sub-info{margin:2px 5px;font-size:smaller}.Bar .show .update-btn{background-color:#1e90ff;cursor:pointer;width:80%;padding:3px;border-radius:5px;border:none;color:#fff;text-align:center}.Bar .show .update-btn:hover{background-color:blue}
.spinner{width:40px;height:40px;position:relative}.double-bounce1,.double-bounce2{width:100%;height:100%;border-radius:50%;background-color:#333;opacity:.6;position:absolute;top:0;left:0;animation:sk-bounce 2s infinite ease-in-out}.double-bounce2{animation-delay:-1s}@keyframes sk-bounce{0%,100%{transform:scale(0);-webkit-transform:scale(0)}50%{transform:scale(1);-webkit-transform:scale(1)}}
.new-update{position:absolute;width:100%;height:100%;background-color:#fff;display:flex;flex-direction:column;align-items:center}.new-update form{position:relative;width:80%;margin:10px;display:flex;flex-direction:column}.new-update form .btn-wrapper{margin:10px}.new-update form .btn-wrapper .btn{padding:5px;margin:0 10px;border:none;border-radius:5px;font-size:medium;cursor:pointer}.new-update form .btn-wrapper .submit{color:#fff;background-color:#20b2aa;opacity:.8}.new-update form .btn-wrapper .submit:hover{opacity:1}.new-update form .btn-wrapper .cancel{color:#fff;background-color:red;opacity:.8}.new-update form .btn-wrapper .cancel:hover{opacity:1}.new-update form .status{position:absolute;height:100%;width:100%;background-color:#fff;cursor:pointer;display:flex;flex-direction:column;align-items:center;justify-content:center}.new-update form .status .message{font-size:large}.new-update form .close{display:none}.close{display:none}
.spinning-logo{max-width:100px;cursor:context-menu;-webkit-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}@media(prefers-reduced-motion: no-preference){.spinning-logo{animation:spinning-logo-spin infinite 15s linear}}@keyframes spinning-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}
